<template>
  <div class="container">
    <div
      class="positon-relative"
      v-scroll-reveal="{ delay: 50, distance: '20px' }"
    >
      <div class="shane_tm_title">
        <div class="title_flex">
          <div class="left">
            <span>Portfolio</span>
            <h3>My projects</h3>
          </div>
        </div>
      </div>
      <!-- End shane_tm_title -->
    </div>

    <div v-if="activetab === 1" class="tabcontent">
      <div class="portfolio_list">
        <ul class="gallery_zoom">
          <li v-for="(image, imageIndex) in allItems" :key="imageIndex">
            <div class="inner" @click="showModal(image)">
              <div class="entry shane_tm_portfolio_animation_wrap">
                <img class="image" :src="image.thumb" alt="Portfolio" />
              </div>
              <!-- End .entry -->
              <div class="mobile_title">
                <h3>{{ image.portfolioName }}</h3>
                <span>{{ image.portfolioCategory }}</span>
              </div>
            </div>
          </li>
          <!-- End li -->
        </ul>
      </div>
      <!-- End .portfolio_list -->
    </div>
    <!-- End .tabcontent 1 -->

    <!-- Start Modalbox -->
    <transition name="fade">
      <!-- Modal -->
      <div
          :class="{ 'modal-mask': isActive }"
          id="modal"
          @click="closeModal"
          v-if="isVisible"
      >
        <div class="modal-dialog modal-dialog-centered " @click.stop>
          <div class="modal-content calltoactions-content">
            <div class="close" @click="closeModal">
              <img
                  class="svg"
                  src="../assets/img/svg/cancel.svg"
                  alt="cancel-img"
              />
            </div>
            <!-- End .close -->

            <div class="shane_tm_mobalbox_contact">
              <div class="box_inner">
                <div class="title">
                  <h3>{{modalData.portfolioName}}</h3>
                </div>
                <div class="wrapper">
                  <div class="left">
                    <p v-html="modalData.portfolioDescription"></p>
                    <hr>
                    <br>
                    <div v-show="modalData.portfolioCompanyEnhancements">
                      <h6>Product enhancements</h6>
                      <br>
                      <div v-for="item in modalData.portfolioCompanyEnhancements" :key="item">
                        <h5><strong>{{ item.title }}</strong></h5>
                        <span v-html="item.content"></span>
                        <br>
                      </div>
                    </div>
                    <div class="project_info">
                      <ul>
                        <li>
                          <div class="list_inner">
                            <img
                                class="svg"
                                src="../assets/img/svg/script.svg"
                                alt="languages"
                            />
                            <p>{{modalData.portfolioLanguage}}</p>
                          </div>
                        </li>
                        <!-- End silgle address -->
                        <li v-show="modalData.githubUrl">
                          <div class="list_inner">
                            <img
                                class="svg"
                                src="../assets/img/svg/github.svg"
                                alt="phone"
                            />
                            <p>
                              <a :href="modalData.githubUrl" target="_blank">Github Page</a>
                            </p>
                          </div>
                        </li>
                        <!-- End silgle address -->
                      </ul>
                    </div>
                  </div>
                  <!-- End left -->
                  <div class="right">
                    <div class="fields">
                      <img class="image" :src="modalData.thumb" alt="Portfolio" />
                    </div>
                  </div>
                  <!-- End right -->
                </div>
                <!-- End modal conetent -->
              </div>
              <!-- End box inner -->
            </div>
            <!-- End modalbox news -->
          </div>
        </div>
      </div>
    </transition>
    <!-- End Modalbox -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      activetab: 1,
      allItems: [
        {
          thumb: require("../assets/img/portfolio/epico.svg"),
          portfolioName: "Epico",
          portfolioCategory: "Enterprise",
          portfolioLanguage: "PHP, JavaScript, MySQL, MongoDB, NodeJS, GoLang, Spring Framework, Docker, Angular",
          portfolioDescription: "Is one of the most complete POS Point of Sale Software on the " +
              "market. With our Point of Sale Software record your sales, make invoices, control inventory and much more.\n" +
              "In addition to bug fixes in existing software and hundreds of implementations in different aspects of the software, my greatest contributions to the company were:",
          portfolioCompanyEnhancements: [
              {
                title: "Software deployment and installation",
                content: "<h6>Before:</h6>" +
                    "<ul>\n" +
                    "  <li>An Apache environment had to be implemented to deploy this application written in PHP.</li>\n" +
                    "  <li>It took about a day to get the software up and running for our customers.</li>\n" +
                    "</ul>" +
                    "<h6>After:</h6>" +
                    "<ul>\n" +
                    "  <li>Installation times were shortened by 90% using Docker containers, allowing the growth of the company.</li>\n" +
                    "</ul>"
              },
              {
                title: "Service for printing and OS-related needs\n",
                content: "<h6>Before:</h6>" +
                    "<ul>\n" +
                    "  <li>There was an application written in Java that carried out the printing process by generating files, detecting them, printing and deleting them, which was a very slow process and hindered the workflow of our clients, in addition to making a lot of use of the speed of the disk, reaching the point of damage them.</li>\n" +
                    "  <li>This service was limited to doing just that, there was no real communication with the software written in PHP and the operating system, due to the limitation of Apache server.</li>\n" +
                    "</ul>" +
                    "<h6>After:</h6>" +
                    "<ul>\n" +
                    "  <li>This was solved by using <strong>GoLang</strong> to create an executable that opened a TCP port (WebServer) to which all print requests were sent, thus speeding up the process because everything was handled in RAM.</li>\n" +
                    "  <li>The implementation of this service allowed <strong>expanding the possibilities</strong> of the software for the needs of the operating system, in addition to speeding up the installation process due to its portability.</li>\n" +
                    "  <li>The compatibility of this service expands to any operating system due to GoLang's cross-platform capability.</li>\n" +
                    "  <li><strong>It became a critical and high performance service for the company.</strong></li>\n" +
                    "</ul>"
              },
              {
                title: "Software licensing system\n",
                content: "<p>The software lacks a licensing system, for this I had to implement one Developed in <strong>Java Spring Framework</strong> as backend and <strong>Angular</strong> as frontend.</p>\n"
              },
              {
                title: "Database Synchronization system\n",
                content: "<h6>Before:</h6>" +
                    "<ul>\n" +
                    "  <li>The system needs to <strong>synchronize databases between local instances and SaaS instances</strong>, a temporary solution was created, which consisted of using ftp, making a backup of the database and uploading it to the server.</li>\n" +
                    "  <li>The system was incomplete, due to the fact that the data on the server was not synchronized with the local</li>\n" +
                    "</ul>" +
                    "<h6>After:</h6>" +
                    "<ul>\n" +
                    "  <li>A solution was created using <strong>Node JS</strong> to create a centralized architecture for synchronizing all clients efficiently and being valid for both parties.</li>\n" +
                    "</ul>"
              },
          ]
        },
        {
          githubUrl: "https://github.com/KstmSoft/kevbot",
          thumb: require("../assets/img/portfolio/kevbot.svg"),
          portfolioName: "KevBot",
          portfolioCategory: "Personal",
          portfolioLanguage: "JavaScript",
          portfolioDescription: "Open source WhatsApp Media bot implementation based on wa-automate-nodejs library, " +
              "download music, convert stickers, search lyrics directly from your chat."
        },
        {
          githubUrl: "https://github.com/KstmSoft/downloader",
          thumb: require("../assets/img/portfolio/downloader.svg"),
          portfolioName: "Bootstrap Downloader",
          portfolioCategory: "Personal",
          portfolioLanguage: "NodeJS",
          portfolioDescription: "Open source YouTube music and videos downloader written in NodeJS with exposed endpoint to serve as API for other clients."
        },
        {
          githubUrl: "https://github.com/KstmSoft/matricula-universitaria",
          thumb: require("../assets/img/portfolio/matricula.svg"),
          portfolioName: "Matricula Universitaria",
          portfolioCategory: "Personal",
          portfolioLanguage: "Java",
          portfolioDescription: "College work written in pure Java to manage courses between students."
        }
      ],
      index: null,
      isActive: false,
      isVisible: false,
      modalData: {}
    };
  },
  methods: {
    showModal: function(data) {
      this.isActive = true;
      this.isVisible = true;
      this.modalData = data;
    },
    closeModal: function() {
      this.isActive = false;
      this.isVisible = false;
    },
  }
};
</script>

<style lang="scss" scoped></style>
